import React, {useState} from "react";
import { CKEditor } from '@ckeditor/ckeditor5-react';
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';

function Vale(props){

    const [respuesta, setRespuesta] = useState(undefined)

    var Articulos = JSON.parse(props.Vale.Articulos);

    

    var x=0;


    function Aprobar(vale){
        
        var url = "https://script.google.com/macros/s/AKfycbwO6J-OmOG_NcryLEQTsIuoHgNHWB8wJTC9ewQ-MaTfzzie0vJYw4ha3GE9YN0TS_tq/exec";

        fetch(url, {
            method:"POST",
            mode: "no-cors",
            cache:"no-cache",
            headers: {"Content-Type":"aplication/json"},
            redirect:"follow",
            body: JSON.stringify({"Respuesta": respuesta, "Command":"AprobarRequisicion", "Vale":vale})
        }).then((response)=>{

            alert("La requisición ha sido aprobada")
        })


    }

    function HandleChange(e){
        setRespuesta(e.target.value)

    }


    return(
        <main style={{display: "flex", justifyContent: "center", flexDirection:"column"}}>
            <div className="ValeFullContaner">
            <section className="DataSection">
                <div>
                    <h3>Usuario</h3>
                    <p>{props.Vale.Usuario}</p>
                </div>
                <div>
                    <h3>Uso</h3>
                    <p>{props.Vale.Uso}</p>
                </div>
                <div>
                    <h3>Folio</h3>
                    <p>{props.Vale.Folio}</p>
                </div>
                <div>
                    <h3>Fecha</h3>
                    <p>{props.Vale.Fecha}</p>
                </div>
            </section>
            <section className="Vale-Table">
            <table className="Vale-Requisicione-Articulos-Header">
                <thead>
                    <th>#Num</th>
                    <th>Descripción</th>
                    <th>Unidades</th>
                    <th>Cantidad</th>
                    <th>Comentario</th>
                
            </thead>
            <tbody>
                {Articulos.map((Articulo)=>{
                    x++
                    return(
                        <tr class="Vale-Requisicion-Articulo">
                            <td>{x}</td>
                            <td>{Articulo.Descripcion}</td>
                            <td>{Articulo.Unidades}</td>
                            <td>{Articulo.Cantidad}</td>
                            <td>{Articulo.Comentario}</td>
                        </tr>
                    )
                    
                    
                })}
            </tbody>
            </table>

            </section>
            <section style={{display:"flex",flexDirection:"column", width: "-webkit-fill-available"}}>
                <label style={{marginTop:"20px"}}>Comentarios</label>
                <div className="editor">
                    <CKEditor
                    editor={ClassicEditor}
                    data = {respuesta}
                    onChange={(event, editor)=>{
                        const data = editor.getData()
                        setRespuesta(data)
                    }}
                    ></CKEditor>
                </div>
                <button onClick={()=>Aprobar(props.Vale)}>Aprobar</button>
                <div style={{marginBottom:"40px"}}></div>
            </section>
            </div>
        </main>
    )


}

export default Vale